.modal-backdrop.fade.show{
  background: $fadeOverlayBrandColor2;
}

#compareBrokersModal{
  .modal-content{
      border: none;
      border-radius: 5px;
    .modal-header{
      border-bottom:none;
    position: absolute;
    right: 10px;
    top: 0;
    z-index: 999;
    }
    .modal-body{
      padding: 2rem;
      h3 {
        margin-bottom: 1.2rem;
      }
      p {
        margin-bottom: 2.6rem;
      }
      ul.provider-list{
        list-style-type: none;
        padding-left:0;
        li{
          .provider-logo{
            img{
              max-width: 130px;
              max-height: 100px;
            }
          }
          .provider-rating{
            img{
              max-width: 150px;
              max-height: 50px;
            }
          }
          .provider-actions{
            .btn{
              font-size: 1rem;
              padding: 0.3rem 0.5rem;
            }
          }
          &:not(:last-child){
            border-bottom: 1px solid $brandColor17;
          }
        }
      }
    }
  }
}